<template>
  <h1 class="disply-4">Quartz.Net Dashboard</h1>

  <div class="container">
    <div class="row">
      <div class="col">
        <div class="input-group mb-3">
          <span class="input-group-text">任務名稱</span>
          <input type="text" class="form-control" v-model="queryParam.jobName" />
        </div>
      </div>
      <div class="col">
        <div class="input-group mb-3">
          <span class="input-group-text">Group</span>
          <input type="text" class="form-control" v-model="queryParam.group" />
        </div>
      </div>
      <div class="col">
        <button class="btn btn-primary">查詢</button>
      </div>
    </div>
  </div>

  <div class="container">
    <div class="col-md-12">
      <div id="css_table" style="background-color: dimgray; color: white; width: 100%">
        <div class="css_tr css_tb_title">
          <div class="css_td" style="width: 95%">設定任務</div>
          <div class="css_td myMOUSE" style="text-align: center" title="新增">
            <i class="fas fa-plus"></i>
          </div>
        </div>
      </div>

      <div id="css_table" style="width: 100%">
        <div class="css_tr css_title">
          <div class="css_td">Group</div>
          <div class="css_td">任務名稱</div>
          <div class="css_td">Start time</div>
          <div class="css_td">上次執行時間</div>
          <div class="css_td">下次執行時間</div>
          <div class="css_td">End Time</div>
          <div class="css_td">狀態</div>
          <div class="css_td">執行計劃</div>
          <div class="css_td">任務描述</div>
          <div class="css_td"></div>
          <div class="css_td"></div>
          <div class="css_td"></div>
          <div class="css_td"></div>
        </div>
        <div class="css_tr" v-for="item in jogs" :key="item">
          <div class="css_td">{{ item.jobGroupName }}</div>
          <div class="css_td">{{ item.jobName }}</div>
          <div class="css_td">{{ item.startTime }}</div>
          <div class="css_td">{{ item.previousFireTime }}</div>
          <div class="css_td">{{ item.nextFireTime }}</div>
          <div class="css_td">{{ item.endTime }}</div>
          <div class="css_td">{{ item.displayState }}</div>
          <div class="css_td">{{ item.scheduleExpression }}</div>
          <div class="css_td">{{ item.description }}</div>
          <div class="css_td myMOUSE" style="text-align: center" title="暫停" :onClick="suspendJob(item.jobGroupName, item.jobName)">
            <i class="fas fa-pause"></i>
          </div>
          <div class="css_td" style="text-align: center" title="恢復執行" :onClick="resumeJob(item.jobGroupName, item.jobName)">
            <i class="fas fa-play"></i>
          </div>
          <div class="css_td" style="text-align: center" titel="立即執行" :onClick="immediatelyExecuteJob(item.jobGroupName, item.jobName)">
            <i class="fas fa-play"></i>
          </div>
          <div class="css_td" style="text-align: center" title="刪除" :onClick="deleteJob(item.jobGroupName, item.jobName)">
            <i class="fas fa-trash"></i>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { ref, onMounted } from "vue";
import myHub from "@/libs/myHub";

export default {
  setup() {
    const jobs = ref([]); //資料集, 放置 Job 資料

    // 查詢參數
    const queryParam = ref({
      group: "", // Group
      jobName: "" // job 名稱
    });

    // 取得 Job 資料集
    const getAllJobs = async () => {
      if (myHub.state.toString() !== "Connected") {
        console.log("SignalR 還未連線");
        await myHub.start();
      }

      myHub
        .invoke("getAllJobs")
        .then(function (rtnValue) {
          console.log("getAllJobs", rtnValue);
          jobs.value = rtnValue.data;
        })
        .catch(function (err) {
          console.log("取得所有 Job 資料錯誤 getAllJobs Error", err.toString());
        });
    };

    // 暫停 Job 的執行, 呼叫 SuspendJob
    const suspendJob = async (jobGroupName, jobName) => {
      if (myHub.state.toString() != "Connected") {
        console.log("SignalR 還未連線");
        await myHub.start();
      }

      myHub
        .invoke("suspendJob", jobName, jobGroupName)
        .then(function (rtnValue) {
          console.log("SuspendJob rtnValue", rtnValue);
          getAllJobs();
        })
        .catch(function (err) {
          console.log("SuspendJob error", err.toString());
        });
    };

    //恢復 Job 的執行
    const resumeJob = async (jobGroupName, jobName) => {
      if (myHub.state.toString() != "Connected") {
        console.log("SignalR 還未連線");
        await myHub.start();
      }

      myHub
        .invoke("resumeJob", jobName, jobGroupName)
        .then(function (rtnValue) {
          console.log("resumeJob rtnValue", rtnValue);
          getAllJobs();
        })
        .catch(function (err) {
          console.log("resumeJob error", err.toString());
        });
    };

    //立即執行
    const immediatelyExecuteJob = async (jobGroupName, jobName) => {
      if (myHub.state.toString() != "Connected") {
        console.log("SignalR 還未連線");
        await myHub.start();
      }

      myHub
        .invoke("immediatelyExecuteJob", jobName, jobGroupName)
        .then(function (rtnValue) {
          console.log("immediatelyExecuteJob rtnValue", rtnValue);
          getAllJobs();
        })
        .catch(function (err) {
          console.log("immediatelyExecuteJob error", err.toString());
        });
    };

    //刪除 Job
    const deleteJob = async (jobGroupName, jobName) => {
      if (myHub.state.toString() != "Connected") {
        console.log("SignalR 還未連線");
        await myHub.start();
      }

      myHub
        .invoke("deleteJob", jobName, jobGroupName)
        .then(function (rtnValue) {
          console.log("deleteJob rtnValue", rtnValue);
          getAllJobs();
        })
        .catch(function (err) {
          console.log("deleteJob error", err.toString());
        });
    };

    // 提供 SignalR Hub 呼叫的方法
    myHub.on("JobStgatusChange", () => {
      console.log("JobStatusChange 被觸發");
      getAllJobs();
    });

    onMounted(() => {
      getAllJobs();
    });

    return {
      jobs, //物件: 回傳 job 資料集
      queryParam, //物件: 查詢參數
      suspendJob, //方法:暫停 Job
      resumeJob, //方法:恢復 Job
      immediatelyExecuteJob, //方法:立即執行
      deleteJob //方法:刪除 Job
    };
  }
};
</script>

<style></style>
